<template>
  <div class="content">
    <img :src="imageUrl ? imageUrl : require('@/assets/logo.png')" alt="vue" />
    <div v-if="isLoading" class="lds-roller">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
    <div class="word-sequence">
      <draggable
        v-model="word"
        @change="compareWords"
        @start="drag = true"
        @end="drag = false"
        v-bind="dragOptions"
      >
        <transition-group
          class="word-sequence"
          type="transition"
          :name="!drag ? 'word-list' : null"
        >
          <div class="letter" v-for="el in word" :key="el.id">{{ el.letter }}</div>
        </transition-group>
      </draggable>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable';

export default {
  name: 'WordGenerator',
  components: {
    draggable,
  },
  data() {
    return {
      word: [],
      wordId: 0,
      text: '',
      imageUrl: null,
      timer: null,
      runtime: 0,
      isLoading: false,
      drag: false,
    };
  },
  methods: {
    /**
     * @property res.data.data.img_src
     */
    newWord() {
      this.isLoading = true;
      this.word = [];
      this.wordId = this.getRandomId();
      this.$store.dispatch('GET_WORD', this.wordId).then(
        (res) => {
          this.isLoading = false;
          this.text = res.data.data.name.toUpperCase();
          this.imageUrl = res.data.data.img_src;
          for (let i = 0; i < this.text.length; i += 1) {
            this.word.push({ id: i, letter: this.text[i] });
          }
          this.word = this.shuffleArray(this.word);
          this.startTimer();
        },
        () => {
          this.newWord();
        },
      );
    },
    shuffleArray(array) {
      const tempArray = array;
      for (let i = tempArray.length - 1; i > 0; i -= 1) {
        const j = Math.floor(Math.random() * (i + 1));
        [tempArray[i], tempArray[j]] = [tempArray[j], tempArray[i]];
      }
      return tempArray;
    },
    startTimer() {
      clearInterval(this.timer);
      this.runtime = 0;
      this.timer = setInterval(() => {
        this.runtime += 1;
      }, 1000);
    },
    getRandomId() {
      const min = 2;
      const max = 1368;
      const id = Math.floor(min + Math.random() * (max + 1 - min));
      if (this.$store.state.words.some(el => el.wordId === id)) {
        this.getRandomId();
      }
      return id;
    },
    compareWords() {
      if (Array.from(this.word, el => el.letter).join('') === this.text) {
        this.$store.commit('ADD_WORD', { wordId: this.wordId, runtime: this.runtime });
        this.$store.commit('OPEN_MODAL');
        clearInterval(this.timer);
      }
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: 'description',
        disabled: false,
        ghostClass: 'ghost',
      };
    },
  },
  mounted() {
    this.newWord();
  },
};
</script>

