  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-flow: column nowrap;
  width: 100%;
  box-sizing: border-box;
  padding: 25px;
}

img {
  max-width: 30%;
  border-radius: 15px;
  margin-top: 20px;
}

.word-sequence {
  margin-top: 2rem;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  user-select: none;
}

.letter {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  background-color: #ffffff;
