import Vue from 'vue';
import Vuex from 'vuex';
import Axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    words: [],
    currentWord: '',
    done: false,
    gameIsOver: false,
  },
  mutations: {
    SAVE_WORD: (state, word) => {
      state.currentWord = word;
    },
    ADD_WORD: (state, payload) => {
      state.words.push({
        id: state.words.length,
        wordId: payload.wordId,
        runtime: payload.runtime,
        word: state.currentWord,
      });
    },
    CLOSE_MODAL: (state) => {
      state.done = false;
    },
    OPEN_MODAL: (state) => {
      state.done = true;
    },
    GAME_IS_OVER: (state) => {
      state.gameIsOver = true;
    },
    CLEAR_ALL: (state) => {
      state.words = [];
      state.currentWord = '';
      state.done = false;
      state.gameIsOver = false;
    },
  },
  actions: {
    GET_WORD: (context, wordId) => new Promise((resolve, reject) => {
      Axios.get(`https://apidir.pfdo.ru/v1/directory-program-activities/${wordId}`).then(
        (res) => {
          if (res.data.data && res.data.data.status === 10) {
            console.log(res.data.data.name);
            context.commit('SAVE_WORD', res.data.data.name);
            resolve(res);
          } else {
            reject(new Error('Не найдено'));
          }
        },
        (rej) => {
          console.error('Rejected:', rej);
        },
      );
    }),
  },
});
