<template>
  <div class="result-content">
    <table class="table">
      <thead>
        <tr>
          <th @click="sortId">
            № <span v-if="sorted === 'idAsc'">&#8593;</span>
            <span v-if="sorted === 'idDesc'">&#8595;</span>
          </th>
          <th @click="sortName">
            Слово <span v-if="sorted === 'nameAsc'">&#8593;</span>
            <span v-if="sorted === 'nameDesc'">&#8595;</span>
          </th>
          <th @click="sortTime">
            Время, с <span v-if="sorted === 'timeAsc'">&#8593;</span>
            <span v-if="sorted === 'timeDesc'">&#8595;</span>
          </th>
        </tr>
      </thead>
      <transition-group tag="tbody" name="word-list">
        <tr v-for="word in words" :key="word.id">
          <td>{{ word.id }}</td>
          <td>{{ word.word }}</td>
          <td>{{ word.runtime }}</td>
        </tr>
      </transition-group>
    </table>
    <p v-if="!words.length">Вы ничего не угадали :(</p>
    <button class="btn btn-replay" @click="$store.commit('CLEAR_ALL')">Начать заново</button>
  </div>
</template>

<script>
export default {
  name: 'ResultTable',
  data() {
    return {
      words: [],
      sorted: '',
    };
  },
  methods: {
    sortId() {
      if (this.sorted === 'idAsc') {
        this.words.sort((a, b) => a.id - b.id);
        this.sorted = 'idDesc';
      } else {
        this.words.sort((a, b) => b.id - a.id);
        this.sorted = 'idAsc';
      }
    },
    sortName() {
      if (this.sorted === 'nameAsc') {
        this.words.sort((a, b) => {
          if (a.word < b.word) {
            return -1;
          }
          if (a.word > b.word) {
            return 1;
          }
          return 0;
        });
        this.sorted = 'nameDesc';
      } else {
        this.words.sort((a, b) => {
          if (b.word < a.word) {
            return -1;
          }
          if (b.word > a.word) {
            return 1;
          }
          return 0;
        });
        this.sorted = 'nameAsc';
      }
    },
    sortTime() {
      if (this.sorted === 'timeAsc') {
        this.words.sort((a, b) => a.runtime - b.runtime);
        this.sorted = 'timeDesc';
      } else {
        this.words.sort((a, b) => b.runtime - a.runtime);
        this.sorted = 'timeAsc';
      }
    },
  },
  mounted() {
    this.words = this.$store.state.words;
  },
};
</script>

